            description:
                'twas time for the chamber city. Routine flight with the RyanAir cowboys, alright landing for them to be honest. Excellent first night in Berlers; a few curryvoorshts then gay smoking chamber for beers and reds. Up nice and fresh on Friday at midday for a treat out of the breakfast rouge dispenser and off to fishmarket south after some excellent coffware. Did some general strolling around some shops and Limeing, went in some questionable shops and ended up at a fine sushware dinner place. Then off to Bergers for two attempts. Im sure you can figure how they went. Off to another smoking chamber then a cocktail chamber to roll-in the night. Once again, up at midday on the Saturday and off to Wilmers caf for a bagers and coffee. Then a stroll down to the parlers but I got kbd so I went a stroll around shops. Place called Lokal for dinner after like three down in some royal chamber bar, probably too wasted to enjoy it as much as it deserved. Onwards to some keeosks for some street beers. Then Pirers. Met a nice coke dealer and Crug got pretty wasted. Ill keep those videos and pictures to ourselves. Even after dinner, chips and a burger at a Rate My Takeaway chamber, and three kebabs; CG was too wasted to get out of bed on the Sunday so Awaz and I went a stroll around the rich district, went to a choccy chamber, and Five Guys for lunch (aha). Finally met CG to go to the one Xmas market that was actually open and downed some mullers and eggers. Then to a beereria finally for a final cooryvoorsht. Early night for the Monday morning flight.',
