            description:
                'Round 115 and 116 at last. Since were hostelmen now, it was time to bag the Glen Nevis YH for some evening leisure and The Prodigal Son, after the Oban dtour. Uncle.Sank a good crate of frizzante too. We had a money-optimizing start to Saturday with CG remaining in slumber until 9:59 then it was off to leven for my first fat dab of the Mamores. Shame, I was kinda hoping my first hit of these would be running all ten in one but, I suppose its too late in the year for that. Anyway, it was good to be running a sea-to-summit (sort of) again after so much time in the Cairny Gs recently. The route was routine, good runnr. The many-different-kind-terrain was also a good brekr in for the fresh rubber. Pack-a-Punched up a Coop meal deal dinner and got ready for the weekend to take a big turn. Another 50 miles to Edinburgh and back to witness a Hard.Style performance with some management passes, a MEGAPINT and thicc milks. Came back down to earth on Sunday though, with the Troon solo followed my Christoph Tile.',
