            description:
                'I should have been pink wojak on this trip bu that fact is, I was just so goddamn in need of my return to projects that nothing could have made this one bad. Over a week that the ALL had been in the geerage meaning I couldnt run the originally intended Cairn Toul proj because of the drive-in and need for an early start. So I booked my first non-west-highland-line highland train ticket and bukked two nights in Hamishs dungeon. The sofa sex chamber. The Aviemore Youth Hostel. My first time soloing a hostel, and doing it right too, with one of those nights in a shared room. Anyway, quiet Friday with one beer and some dev. Also some dood had a shared rant with me about ThinkPads, was excellent. Twas the bus the next morning to the Ski Centre car park to take off on by backup plan, Cairn Gorm and Macdui. Excellent conditions straight into the very unexpected, but glorious, snow storm. Bit disappointing though, 60mph snow gusts stopped me from getting over to Macdui again. So I had a quick coffee and ran the walk-out, in a very Just Cause 2 climate-change-like fashion. Another quiet night cooking up a storm, running Mambos for two, then soloing a crate and Star Wars in the currum. Didnt get to do much on the Sunday, train times werent very forgiving. Tried Route 7. Denied. So just browsed a few outdoor shops and went home. Much. Needed.',
