            description:
                'As always, this was an interesting one. Back in the feisty sta for the A9 run-in and a camp in Glenmore. Ran a few down in the Pine (PMB). Next morning off to the Oxy caravan to be greeted by Supreme Emperor Ian and wife for a brioche bacon roll. Then it was time to part-ways with the battalion and head to Mambos for a dissertation sesh. Good cream of broccoli soup lunch too. Then the battalion re-assembled to become Beer and TacosMen. It was back to dissertation, frizzante and being a DrumsMan for me; missing it all kicking off with Miss Martin in the Marten. Sunday was slow as usual. Some free brekkie dood in the hostel followed by a Bruar lunch and immensely dull walk in Pitlochry on the drive-out.',
