            names: ['Skyfall  Pause'],
            startDate: '23rd of July',
            endDate: '24th of July',
            description:
                'Ben Strava.. The fine breasted lady with one leg and no head. At least thats what the GPX file suggests. What an amazing route. Some of the best views to date, and my first full inversion after Ben More and Stob Binneins partial. It was a classic start with a rice sachet and monsterous portion of reduced Morrisons deli potatoes, along with some How I Met Your Mother, in the Saab dining room / cinema theatre. After the Miami Vice Brothers Keeper feature length pilot, it was off to the Saabs double bed all to myself while Craig pitched. It was a midge-ridden start to the round on Saturday but once Id battled off my fatigue from the week of 3am starts and Fenwick-Wind Farm loops and wed made our way through the fort of ferns and low-level clag, it was straight into a clean white blanket of cloud enclosing Glencoeand Glen Etive with all the famous peaks showing their heads.. Sloggy grass turned to dry scrambly rock and humid clag to a clear sky and warm breeze. Sadly cut short because of a severe lack of fill up points.',
