            startDate: '15th of July',
            endDate: '',
            description:
                'I consider this project my greatest achievement to date. Not only in the hills or in sport but, in life. At 23.94 miles and 9,162 feet, this 13.5 hour day is by far one of the most enduring, satisfying and beautifully rewarding Ive ever experienced. It was part flex, part chad, and part Ive just had my Coooviiid Noynteen vaccine and want to prove it doesnt effect me. I was also recovering from a flu that I got, from the dumb 3am starts at my wagey job cucking and grinding for the paper (later leading to the A6 C6 TDV allroad TDI quattro, however), and grinding daily Strathaven cycles. Anyway, no need to complain, I deserve no sympathy. This was a damn good day. The Arrochar Alps still get a bad rep amongst more serious enthusiasts or baggers (actually soys). However, how could you possibly deny the beauty and versatility of the playground that is these stunning four Munros, two Corbetts, and Corbett top? Especially when theyre basically in the back yard of the major western cities. The payoff for such a cheap day out is unmatched. Of course en route to having only seven minutes to spare at the train station, this route also saw the famous coining of the A82 Run-Out and its components: Slingshot, Freerun Frenzy, Tightrope Walk 1, and Tightrope Walk 2.',
